.home{
    display: grid;
    grid-template-rows: 7vh 13vh;
    background-color: #000000; 
}

.header-home{
    /* background-color: #ac12ac; */
    background-color: #000000;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    border-bottom: 2px solid #363636;
}

.header-home img{
    height: 75%;
    margin-left: 12px;
}

.header-home h2{
    margin-left: 16px;
    color: white;
    font-weight: bolder;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.story-home{
    /* background-color: rgb(244, 113, 244); */
    background-color: #121212;
    border-bottom: 2px solid #363636;
}

.myStory{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 4px;
    margin-left: 10px;
}

.myStory h5{
    margin-bottom: 4px;
}



.post-home{
    /* background-color: rgb(244, 113, 244); */
    background-color: #000000; 
}

.suggestion-home{
    display: flex;
    border-top: 2px solid #363636;
    border-bottom: 2px solid #363636;
    /* background-color: rgb(244, 113, 244); */
    background-color: #121212; 
    align-items: center;
    margin-top: 20px;
    overflow-y: auto;
    white-space: nowrap;
    padding: 10px 0;
}

.sugg-post-text{
    color: white;
    border-bottom: 1px solid #363636;
    padding: 12px 0;
    margin-top: 5px;
    padding-left: 12px;
}

