.editeProfile-container{
    min-height: 100vh;
    /* background-color: rgb(244, 113, 244); */
    background-color: #000000;
}

.avatar-edit-container{
    display: flex;
    padding-left: 10px;
    margin-top: 10px;
}

.avatar-input{
    display: flex;
    flex-direction: column;
    margin-left: 15px;
}

.username-avatar-name{
    color: white;
    font-size: 18px;
}

.avatar-input-ref{
    color: #0095f6;
}

.username-input-contralls, .email-input-contralls{
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 20px;
}

.username-input-contralls h4, .email-input-contralls h4{
    color: white;
}

.username-input-contralls input, .email-input-contralls input{
    margin-top: 5px;
    width: 100%;
    padding: 8px 6px;
    background-color: #262626;
    border: none;
    outline: none;
    color: white;
    font-size: 18px;
}

.edit-submit-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.edit-submit-btn button{
    padding: 10px 4px;
    background-color: #0095f6;
    border: 1px solid #0095f6;  
    width: 27%;
    color: white;
    font-weight: bold;
    border-radius: 8px;
}