
.abcd{
    width: 100vw;
}

.input-choice{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.setImage-icon svg{
    color: whi;
    font-size: 40px;
}

/* .cropper-conatiner{
