.final-post-page{
    min-height: 95vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.caption-input{
    display: flex;
    flex-direction: column;
    width: 80%;
}

.caption-input input{
    margin-top: 5px;
    border: none;
    outline: none;
    width: 100%;
    border-radius: 6px;
    border: 1px solid #a8a8a8;
    background-color: black;
    padding: 4px 5px;
    color: white;
}

.post-btn{
    background-color: #0095f6;
    border: 1px solid #0095f6;
    margin-top: 15px;
    width: 24%;
    padding: 10px;
    border-radius: 8px;
    color: white;
    font-weight: bold;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}