.container2{
    background-color: #131324;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-signin{
    background-color: #00000076;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90vw;
    height: 75vh;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.header{
    height: 15vh;
    display: flex;
    /* justify-content: space-around; */
    text-align: center;
    width: 100%;
    margin-top: -55px;
    margin-left: 45px;
}

.header img{
    height: 45%;
    margin-top: 20px;
    margin-left: 55px;
}

.header h1{
    color: white;
    text-transform: uppercase;
    margin-top: 19px;
    margin-left: 20px;
}

.first-input{
    margin-top: -19px !important;
}

.input{
    background-color: transparent;
    color: white;
    margin: 16px auto;
    padding: 9px 10px;
    outline: none;
    border: 2px solid #4e0eff;
    border-radius: 8px;
    width: 65%;
    font-size: 15px;
}

.input:focus{
    border: 0.1rem solid #997af0;
    outline: none;
}

.btn{
    margin: 10px auto;
    background-color: #997af0;
    color: white;
    padding: 10px;
    border: none;
    font-weight: bold;
    cursor: pointer;
    border-radius: 8px;
    width: 65%;
}

.form-text{
    color: white;
    width: 100%;
    text-align: center;
    margin-top: 20px;
}

.acc-text{
    margin-left: 10px;
    cursor: pointer;
}

.login-link{
    text-decoration: none;
    font-weight: bold;
}

/* .avatar{
    border-radius: 50%;
} */

.avatar-container{
    display: flex;
    justify-content: center;
    margin-top: -28px;
}

.avatar-image{
    position: relative;
}

.avatar-select{
    position: absolute;
    margin-left: 50px;
    margin-top: 50px;
}


@media only screen and (max-width: 1000px){
    .form{
        width: 60vw;
    }
}

@media only screen and (max-width: 585px){
    .form{
        width: 80vw;
        margin-top: -65px;
    }
}

@media only screen and (max-width: 400px){

    .form{
        /* position: fixed; */
        margin-top: 18px;
        width: 85vw;
        height: 80vh;
    }

    .header{
        position: sticky !important;
    }

    .header img{
        margin-left: 0px;
        /* margin-top: 10PX; */
    }

    .header h1{
        margin-top: 25px;
    }

    .input{
        width: 80%;
        padding: 10px;
    }

    .btn{
        width: 80%;
    }

    .toast-style{
        margin: 10px auto;
        width: 90vw;
        border-radius: 10px;
    }

    .form-text{
        margin-top: 25px;
    }
}