.profile-detail{
    display: grid;
    grid-template-columns: 35vw 65vw;
    /* background-color: rgb(244, 113, 244); */
    background-color: #000000;
    border-bottom: 1px solid #363636;
}

.left-deatile{
    width: 100%;
    /* height: 18vh; */
    display: flex;
    flex-direction: column;
    padding-left: 10px;
}

.avatar-detail{
    margin-top: 12px;
}

h5{
    margin-top: 12px;
    margin-left: 5px;
    color: white;
}

.right-detail{
    width: 100%;
    /* height: 18vh; */
    display: flex;
    flex-direction: column;
}


p{
    font-size: 30px;
    color: white;
    margin-top: 12px;
}

.edit-profile{
    width: 220px;
    padding: 5px 9px;
    margin-top: 8px;
    border-radius: 5px;
    border: none;
    font-size: 14px;
}

.follow-following{
    margin: 10px 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-top: 1px solid #363636;
    padding-top: 4px;
}

h3{
    margin: 3px 0;
}

.post-detail, .followers-detail, .following-detail{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    align-items: center;
}

a{
    color: white;
    text-decoration: none;
}