.explore{
    /* background-color: rgb(244, 113, 244); */
    background-color: #000000;
    min-height: 100vh;
}

.all-explore-posts{
    display: grid;
    grid-template-columns: 32.5% 32.5% 32.5%;
    column-gap: 1%;
    row-gap: 0.2%;
    margin-top: 3px;
}

.explore-image-div{
    height: 117px;
    position: relative;
    box-sizing: border-box;
    background-color: #262626;
}

.img-explore{
    width: 100%;
    vertical-align: middle;
    height: inherit;
    background-color: #262626;
}