.comment-container{
    min-height: 100vh;
    background-color: #000000;
}

.create-comment{
    display: flex;
    align-items: center;
    background-color: #262626;
    height: 60px;
    justify-content: space-around;
}

.comment-input{
    display: flex;
    align-items: center;
    /* background-color: rgb(244, 113, 244); */
    background-color: #000000;
    width: 77vw;
    height: 38px;
    /* border:  2px solid #800080; */
    border-radius: 25px;
}

.comment-input input{
    background-color: #000000;
    margin-left: 15px;
}

.input-comment-text{
    background-color: rgb(244, 113, 244);
    border: none;
    outline: none;
    width: 80%;
    color: white;
    margin-left: 7px;
}

.comment-btn{
    color: #800080;
    font-weight: bolder;
}

.all-comment{
    background-color: #000000;
    min-height: 80vh;
}

.post-owner{
    display: flex;
    margin-top: 12px;
    align-items: center;
    background-color: #000000;
}

.ownwr-img{
    margin-left: 12px;
}

.post-ownwr-id{
    color: white;
    font-weight: bolder;
    font-size: 20px;
    margin-left: 25px;
}

.ownwr-comment{
    color: white;
    margin-left: 17px;
}

.delete-container{
    display: flex;
    align-items: center;
    position: absolute;
    right: 10px;
}

#new-comment{
    display: none;
}