.footer{
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 7vh;
    /* background-color: #800080; */
    background-color: #000000;
    /* display: grid;
     grid-template-columns: 20% 20% 20% 20% 20%;
     align-self: center; */
      display: flex;
     border-top: 1px solid #363636;
