.suggestion-card{
    display: block;
    margin-top: 20px;
    min-width: 60vw;
    height: 40vh;
    /* background-color: #800080; */
    background-color: #000000;
    border: 1px solid #363636;
    border-radius: 10px;
    margin: 0 10px;
}

.avatar-suggestion{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 7rem;
    margin-top: 10px;
}


.avatar-suggestion .img{
    border: 2px solid black;
}

.username-suggestion{
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.btn-suggestion{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-content: center;
}

.btn-suggestion button{
    padding: 7px 20px;
    width: 100px;
    background-color: #0095f6;
    border: 1px solid #0095f6;
    border-radius: 10px;
    margin-top: 20px;
    color: #ffffff;
}
