.model-conatiner{
    /* background-color: #f471f41*/
    background-color: #262626;
    width: 75vw;
    height: 94vw;
    border-radius: 10px;
    position: fixed;
    top: 30%;
    left: 14%;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.popup{
    text-align: center;
    width: 100%;
    /* margin: 5px 0; */
    border-bottom: 1px solid #363636;
    padding-bottom: 10px;
    font-weight: bold;
    color: #ffffff;
}

.Report-popup, .Unfollow-popup, .Delete-popup {
    color: red;
}

.Cancel-popup{
    text-align: center;
    width: 100%;
    /* margin: 5px 0; */
    font-weight: bold;
    color: #ffffff;
}