.post-post{
    margin-bottom: 20px;
    position: relative;
}

/* .model-post{
    
} */

.post-post .post-header{
    display: flex;
    /* background-color: #ac12ac; */
    background-color: #000000;
    padding: 8px 0;
    padding-left: 7px;
    align-items: center;
    border-bottom: 1px solid #363636;
}

.post-header .post-owner-name{
    margin-left: 10px;
    color: white;
    font-weight: 500;
}

.post-header .post-three-dot{
    color: white;
    position: absolute;
    right: 16px;
}

.post-image{
    display: flex;
    justify-content: center;
    width: 100vw;
    background-color: #000000;
}

.post-image img{
    width: 100%;
}

.post-post .post-detail2{
    display: flex;
    flex-direction: column;
    /* background-color: #800080; */
    background-color: #000000; 
}

.post-post .post-detail2 .post-icons svg{
    color: white;
    margin: 7px 5px;
    font-size: 28px;
}

.total-post-likes{
    color: white;
    margin-left: 7px;
    font-weight: 500;
    text-decoration: none;
}

.post-detail2 .post-owner .post-owner-name{
    margin-left: 7px;
    color: white;
    font-weight: 500;
}

.post-detail2 .post-owner .post-owner-name .post-caption{
    font-size: 18px;
}

.total-post-comments{
    color: rgb(204, 200, 200);
    margin-left: 7px;
    font-weight: 500;
    margin-top: 10px;
}
