.update-post-container{
    width: 100vw;
    height: 100vh;
    background-color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.img-div{
    margin-top: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
}

.image{
    width: 70%;
}

.caption-input2{
    margin-top: 10px;
    width: 73%;
    padding: 8px 6px;
    color: white;
    background-color: #262626;
    border: 2px solid #363636;
    border-radius: 8px;
    outline: none;
}

.caption-btn{
    margin-top: 10px;
    padding: 10px 4px;
    background-color: #0095f6;
    border: 1px solid #0095f6;  
    width: 27%;
    color: white;
    font-weight: bold;
    border-radius: 8px;
}