.landing-load{
    display: flex;
    flex-direction: column;
}

.landing-load .app-logo{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 80vh;
    background-color: white;
}

.landing-load .app-logo img{
    width: 22%;
}

.landing-load .app-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 20vh;
    background-color: white;
}

.landing-load .app-info h4{
    /* color: grey; */
    color: #c79be5;
}

.landing-load .app-info h2{
    
    color: #590cac;
}

